"use client"

import { Navbar } from "@/components/navbar"
import { Footer } from "@/components/footer"
import { motion } from "framer-motion"
import { 
  Sparkles, 
  Bug, 
  Wrench, 
  Zap,
  ChevronRight,
  Calendar,
  Tag,
  Loader2,
  AlertCircle
} from "lucide-react"
import { useState } from "react"
import Link from "next/link"
import useSWR from "swr"

type ChangeType = "feature" | "fix" | "improvement" | "performance"

interface Change {
  type: ChangeType
  title: string
  description: string
}

interface ChangelogEntry {
  id: string
  version: string
  date: string
  title: string
  description: string
  changes: Change[]
}

interface ChangelogResponse {
  success: boolean
  currentVersion: string
  changelogs: ChangelogEntry[]
  total: number
}

const fetcher = (url: string) => fetch(url).then(res => res.json())

const changeTypeConfig: Record<ChangeType, { icon: typeof Sparkles; color: string; bg: string; label: string }> = {
  feature: { icon: Sparkles, color: "text-emerald-400", bg: "bg-emerald-500/10", label: "Novo" },
  fix: { icon: Bug, color: "text-red-400", bg: "bg-red-500/10", label: "Correcao" },
  improvement: { icon: Wrench, color: "text-blue-400", bg: "bg-blue-500/10", label: "Melhoria" },
  performance: { icon: Zap, color: "text-yellow-400", bg: "bg-yellow-500/10", label: "Performance" },
}

function formatDate(dateString: string): string {
  const date = new Date(dateString)
  return date.toLocaleDateString("pt-BR", {
    day: "2-digit",
    month: "short",
    year: "numeric",
  })
}

function ChangelogCard({ entry, index, currentVersion }: { entry: ChangelogEntry; index: number; currentVersion: string }) {
  const [isExpanded, setIsExpanded] = useState(false)
  const isLatest = entry.version === currentVersion

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.6, delay: index * 0.1 }}
      className="relative"
    >
      {/* Timeline line - hidden on mobile */}
      <div className="absolute left-[19px] top-12 bottom-0 w-px bg-zinc-800 hidden lg:block" />
      
      <div className="flex gap-4 lg:gap-6">
        {/* Timeline dot - hidden on mobile */}
        <div className={`hidden lg:flex flex-shrink-0 w-10 h-10 rounded-full ${isLatest ? "bg-emerald-500/30 border-emerald-500/50" : "bg-emerald-500/20 border-emerald-500/30"} border items-center justify-center`}>
          <div className={`w-3 h-3 rounded-full ${isLatest ? "bg-emerald-400" : "bg-emerald-500"}`} />
        </div>

        {/* Card */}
        <div className="flex-1 mb-4 sm:mb-6 lg:mb-8">
          <div 
            className={`bg-zinc-900/50 border rounded-xl sm:rounded-2xl overflow-hidden transition-all duration-300 ${
              isExpanded ? "border-emerald-500/30" : isLatest ? "border-emerald-500/20" : "border-zinc-800 hover:border-zinc-700"
            }`}
          >
            <button
              type="button"
              onClick={() => setIsExpanded(!isExpanded)}
              className="w-full p-4 sm:p-6 text-left"
            >
              <div className="flex flex-wrap items-center gap-2 sm:gap-3 mb-3 sm:mb-4">
                <span className={`px-2.5 sm:px-3 py-1 rounded-full ${isLatest ? "bg-emerald-500/20 text-emerald-300" : "bg-emerald-500/10 text-emerald-400"} text-xs sm:text-sm font-medium flex items-center gap-1`}>
                  <Tag className="w-3 h-3" />
                  v{entry.version}
                  {isLatest && <span className="ml-1 text-xs">(atual)</span>}
                </span>
                <span className="text-zinc-500 text-xs sm:text-sm flex items-center gap-1">
                  <Calendar className="w-3 h-3" />
                  {formatDate(entry.date)}
                </span>
              </div>
              <h3 className="text-white font-medium text-lg sm:text-xl mb-1.5 sm:mb-2">{entry.title}</h3>
              <p className="text-zinc-400 text-xs sm:text-sm leading-relaxed">{entry.description}</p>
              <div className="flex items-center gap-2 mt-3 sm:mt-4 text-emerald-400 text-xs sm:text-sm">
                <span>{isExpanded ? "Ver menos" : `Ver ${entry.changes.length} mudancas`}</span>
                <ChevronRight className={`w-4 h-4 transition-transform ${isExpanded ? "rotate-90" : ""}`} />
              </div>
            </button>

            {/* Expanded content */}
            <div className={`overflow-hidden transition-all duration-300 ${isExpanded ? "max-h-[1500px]" : "max-h-0"}`}>
              <div className="px-4 sm:px-6 pb-4 sm:pb-6 border-t border-zinc-800 pt-4 sm:pt-6">
                <div className="space-y-3 sm:space-y-4">
                  {entry.changes.map((change) => {
                    const config = changeTypeConfig[change.type]
                    const Icon = config.icon
                    return (
                      <div key={change.title} className="flex gap-3 sm:gap-4">
                        <div className={`flex-shrink-0 w-7 h-7 sm:w-8 sm:h-8 rounded-lg ${config.bg} flex items-center justify-center`}>
                          <Icon className={`w-3.5 h-3.5 sm:w-4 sm:h-4 ${config.color}`} />
                        </div>
                        <div className="min-w-0 flex-1">
                          <div className="flex flex-wrap items-center gap-1.5 sm:gap-2 mb-0.5 sm:mb-1">
                            <span className={`text-xs font-medium ${config.color}`}>{config.label}</span>
                            <span className="text-white font-medium text-sm sm:text-base break-words">{change.title}</span>
                          </div>
                          <p className="text-zinc-500 text-xs sm:text-sm break-words">{change.description}</p>
                        </div>
                      </div>
                    )
                  })}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </motion.div>
  )
}

export default function ChangelogPage() {
  const { data, error, isLoading } = useSWR<ChangelogResponse>("/api/changelog", fetcher)

  return (
    <main className="min-h-screen bg-[#09090B] overflow-x-hidden">
      <Navbar />
      
      {/* Hero Section */}
      <section className="pt-24 sm:pt-32 pb-12 sm:pb-20 px-4 sm:px-6">
        <div className="max-w-5xl mx-auto text-center">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
          >
            <div className="inline-flex items-center gap-2 px-3 sm:px-4 py-1.5 sm:py-2 rounded-full bg-emerald-500/10 border border-emerald-500/20 mb-4 sm:mb-6">
              <Sparkles className="w-3.5 h-3.5 sm:w-4 sm:h-4 text-emerald-400" />
              <span className="text-emerald-400 text-xs sm:text-sm font-medium">Change Logs</span>
            </div>
            <h1 
              className="text-3xl sm:text-4xl md:text-5xl lg:text-6xl text-white mb-4 sm:mb-6 px-2"
              style={{
                letterSpacing: "-0.0325em",
                fontVariationSettings: '"opsz" 28',
                fontWeight: 538,
                lineHeight: 1.1,
              }}
            >
              Novidades do<br />
              <span className="text-emerald-400">fluxpay</span>
            </h1>
            <p className="text-zinc-400 text-base sm:text-lg max-w-2xl mx-auto leading-relaxed px-2">
              Acompanhe todas as atualizacoes, novos recursos e melhorias que estamos
              trazendo para tornar sua experiencia ainda melhor.
            </p>
            {data?.currentVersion && (
              <p className="text-zinc-600 text-xs sm:text-sm mt-3 sm:mt-4">
                Versao atual: <span className="text-emerald-400 font-medium">v{data.currentVersion}</span>
              </p>
            )}
          </motion.div>
        </div>
      </section>

      {/* Subscribe Section */}
      <section className="pb-12 sm:pb-16 px-4 sm:px-6">
        <div className="max-w-xl mx-auto">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
            className="bg-zinc-900/50 border border-zinc-800 rounded-xl sm:rounded-2xl p-4 sm:p-6"
          >
            <p className="text-zinc-400 text-xs sm:text-sm mb-3 sm:mb-4 text-center">Receba novidades por email</p>
            <form className="flex flex-col sm:flex-row gap-3" onSubmit={(e) => e.preventDefault()}>
              <input
                type="email"
                placeholder="seu@email.com"
                className="flex-1 px-3 sm:px-4 py-2.5 sm:py-3 bg-zinc-800/50 border border-zinc-700 rounded-lg sm:rounded-xl text-white text-sm placeholder:text-zinc-500 focus:outline-none focus:border-emerald-500/50"
              />
              <button 
                type="submit"
                className="px-5 sm:px-6 py-2.5 sm:py-3 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 text-sm font-medium rounded-lg sm:rounded-xl transition-colors"
              >
                Inscrever
              </button>
            </form>
          </motion.div>
        </div>
      </section>

      {/* Changelog Timeline */}
      <section className="py-12 sm:py-20 px-4 sm:px-6">
        <div className="max-w-3xl mx-auto">
          {isLoading ? (
            <div className="flex flex-col items-center justify-center py-16 sm:py-20">
              <Loader2 className="w-6 h-6 sm:w-8 sm:h-8 text-emerald-400 animate-spin mb-3 sm:mb-4" />
              <p className="text-zinc-500 text-sm">Carregando atualizacoes...</p>
            </div>
          ) : error ? (
            <div className="flex flex-col items-center justify-center py-16 sm:py-20">
              <AlertCircle className="w-6 h-6 sm:w-8 sm:h-8 text-red-400 mb-3 sm:mb-4" />
              <p className="text-zinc-400 text-sm mb-2">Erro ao carregar atualizacoes</p>
              <p className="text-zinc-600 text-xs">Tente novamente mais tarde</p>
            </div>
          ) : data?.changelogs && data.changelogs.length > 0 ? (
            data.changelogs.map((entry, index) => (
              <ChangelogCard 
                key={entry.id} 
                entry={entry} 
                index={index} 
                currentVersion={data.currentVersion}
              />
            ))
          ) : (
            <div className="flex flex-col items-center justify-center py-16 sm:py-20">
              <Sparkles className="w-6 h-6 sm:w-8 sm:h-8 text-zinc-600 mb-3 sm:mb-4" />
              <p className="text-zinc-400 text-sm">Nenhuma atualizacao disponivel</p>
            </div>
          )}
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-12 sm:py-20 px-4 sm:px-6">
        <div className="max-w-3xl mx-auto text-center">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
            className="bg-gradient-to-b from-zinc-900/80 to-zinc-900/50 border border-zinc-800 rounded-2xl sm:rounded-3xl p-8 sm:p-12"
          >
            <h2 
              className="text-xl sm:text-2xl md:text-3xl lg:text-4xl text-white mb-3 sm:mb-4"
              style={{
                letterSpacing: "-0.0325em",
                fontVariationSettings: '"opsz" 28',
                fontWeight: 538,
              }}
            >
              Tem sugestoes?
            </h2>
            <p className="text-zinc-400 text-sm sm:text-base mb-6 sm:mb-8 max-w-md mx-auto px-2">
              Adoramos ouvir nossos usuarios. Envie suas ideias e ajude a moldar o futuro do fluxpay.
            </p>
            <Link
              href="/como-funciona"
              className="inline-flex items-center gap-2 px-5 sm:px-6 py-2.5 sm:py-3 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 text-sm font-medium rounded-lg sm:rounded-xl transition-colors"
            >
              Enviar feedback
              <ChevronRight className="w-4 h-4" />
            </Link>
          </motion.div>
        </div>
      </section>

      <Footer />
    </main>
  )
}
